:root {
  --light: #fff;
  --dark: #2d2e2e;
  --dark-light: #474B56;
  --decor: #F5F9FE;
  --accent: #0C93F4;
  --text: #5F6061;
  --light-text: #929292;
  --transparent: rgba(255, 255, 255, 0);
  --disclaimer: #77838E;
  --card-bg: #EBF8FF;
  --star: #FFE55B;
  --speed: 300ms;
  --time-function: ease-in-out;
  --base-font: "Roboto Flex";
  --second-font: 'Roboto Condensed';
  --vh: 1vh;
  --rounded: 24px;
  --big-rounded: 45px;
  --gap: 36px;
  --padding: 20px;
  --desktop-width: 1560px;
  --site-container: calc(var(--desktop-width) + (var(--padding) * 2));
}

